        currentCategory: 'ALL',
      }
  },
  filters: {
    dateFilter: function (value) {
      return moment(value).format('D MMMM YYYY');
    }
  },
  computed: {
    tableData: function () {
      return mockingData.slice(0, 10);
    }
  },
  methods: {
    setNav: function (navName) {
      this.currentCategory = navName;
      return false;
    },
    getCategoryClass: function (category) {
      switch (category) {
        case 'ALL':
          return 'cate-all';
        case 'プレスリリース':
          return 'cate-press-release';
        case 'コンテンツ／コラム':
