              </li>
              <li class="nav-item">
                <a class="nav-link cate-press-release" href="#" @click.prevent="setNav('プレスリリース')" :class="{'active': currentCategory=='プレスリリース'}">プレスリリース</a>
              </li>
              <li class="nav-item">
                <a class="nav-link cate-content-column" href="#" @click.prevent="setNav('コンテンツ／コラム')" :class="{'active': currentCategory=='コンテンツ／コラム'}">コンテンツ／コラム</a>
