      </div>
      <div class="row">
        <div class="col">
          <ul class="list-group">
            <a href="#" class="list-group-item list-group-item-action" v-for="item in tableData" :key="item.id">
              <p class="info">
                <span class="date">{{item.date | dateFilter}}</span> | <span class="category" :class="getCategoryClass(item.category)">{{item.category}}</span>
              </p>
              <p class="title">{{item.title}}</p>
            </a>
          </ul>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import mockingData from 'mock/news_release.json'
import moment from 'moment'

export default {
  name: 'HomeNewsRelease',
  data(){
      return{
        currentCategory: 'ALL',
      }
  },
  filters: {
    dateFilter: function (value) {
      return moment(value).format('D MMMM YYYY');
    }
  },
  computed: {
    tableData: function () {
      let retval = []
      if (this.currentCategory == 'ALL') {
        retval = mockingData;
      } else {
        retval = mockingData.filter(x => {
          return x.category == this.currentCategory;
        });
      }
      return retval;
    }
  },
  methods: {
    setNav: function (navName) {
      this.currentCategory = navName;
      return false;
    },
    getCategoryClass: function (category) {
      switch (category) {
        case 'ALL':
          return 'cate-all';
        case 'プレスリリース':
          return 'cate-press-release';
        case 'コンテンツ／コラム':
          return 'cate-content-column';
        case 'メディア':
          return 'cate-media';
        case '投資情報':
          return 'cate-investment-info';
        case '採用／その他':
          return 'cate-recruitment-others';
      }
    }

  }
}
</script>

